@import url('https://fonts.googleapis.com/css2?family=Poppins:display=swap');

:root{
    --primary:#04445f;
    --secondary:#008ac5;
    --pre-color:#c6dae2;
    --background:whitesmoke;
    --sm-pad:10px;
    --headers-font-2: 'Poppins',sans-serif;
}
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.home-container{
    width: 100%;
    min-height: 90.5vh;
}
.home-column-1{
    width: 100%;
    height: 90vh;
    background:var(--background);
    padding-left: 40px;
    padding-right: 40px;
    display: flex;
    align-items: center;
}
.home-column-1-1{
    width: 55%;
    height: 100%;
    padding-top: 40px;
}
.home-column-1-2{
    width: 45%;
    height: 100%;
    position: relative;
    padding-left: 10px;
    padding-top: 110px;
}
.home-column-1-header{
    font-size: 35px;
    font-weight: 500;
    color: var(--primary);
}
.home-column-1-p{
    margin-top: 30px;
    color: black;
}
.home-column-1-header span{
    color: var(--secondary);
}
.home-column-1-links-container{
    width: 60%;
    margin-top: 50px;
    display: flex;
}
.home-link{
    display: inline-block;
    width: 500px;
    padding: 10px;
    text-align: center;
    text-decoration: none;
    border-radius: 5px;
    color: white;
    font-size: 20px;
    background-color: var(--primary);
}
.home-column-1-1-2{
    width: 100%;
    height: 11vh;
    margin-top: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.home-column-1-1-2-column{
    width: 400px;
    height: 100%;
    text-align: center;
    border-left: 1px solid var(--primary);
}
.home-column-1-1-2-column:first-child{
    border: none;
    padding-left: 10px;
    text-align: left;
}
.home-column-1-1-2-column h2{
    margin-left: 20px;
    font-size: 30px;
    color: var(--primary);
}
.home-column-1-1-2-column p{
    margin-top: 10px;
}
.home-link:last-child{
    margin-left: 30px;
}
.home-link:hover{
    background-color: var(--secondary);
}
.home-column-1-2-box{
    position: absolute;
    width: 80%;
    height: 45%;
    z-index: 2;
    background: var(--primary);
    border-radius: 30px;
}
.home-column-1-2-box-2{
    position: absolute;
    width: 80%;
    height: 45%;
    background-image: url("../assets/images/IMG-20230304-WA0022.jpg");
    background-position: center;
    background-size: cover;
    margin-top: 55px;
    z-index: 3;
    margin-left: 55px;
    border-radius: 30px;
}
.home-column-1-2-box-3{
    position: absolute;
    width: 80%;
    height: 45%;
    background: var(--pre-color);
    z-index: 1;
    border-radius: 30px;
    top: 38%;
    left: 20%;
}
.home-column-2-1{
    display: none;
}

@media screen and (max-width:786px){
    .home-column-1{
        height: 90vh;
        display: block;
        padding-left: var(--sm-pad);
        padding-right: var(--sm-pad);
        position: relative;
    }
    .home-column-1-1{
        width: 100%;
        padding-top: 0;
        min-height: 100vh;
    }
    .home-column-1-2{
        padding-top: 0;
        width: 100%;
        height: 50%;
        position: absolute;
        background: yellow;
        bottom: 0;
        display: none;
    }
    .home-column-1-2-box{
        width: 80%;
        height: 80%;
    }
    .home-column-1-2-box-2{
        width: 80%;
        height: 80%;
    }
    .home-column-1-header{
        font-size: 25px;
        font-family: var(--headers-font-2);
        padding-top: 40px;
    }
    .home-column-1-p{
        font-size: 15px;
        color: black;
    }
    .home-column-1-p-close{
        display: none;
    }
    .home-column-2{
        padding: 0;
        display: flex;
        min-height: 160vh;
        align-items: center;
        justify-content: center;
    }
    .home-column-2-1-boxes{
    }
    .home-column-1-links-container{
        width: 100%;
        display: block;
        margin-top: 60px;
    }
    .home-column-1-links-container{
        width: 100px;
    }
    .home-link{
        width: 230px;
        padding: 10px;
        margin-top: 35px;
        font-size: 20px;
    }
    .home-link:hover{
        background: var(--primary);
    }
    .home-link:last-child{
        margin-left: 0;
    }
    .home-link:first-child{
        margin-top: 0;
    }
    .home-column-1-p{
        margin-top: 20px;
    }
    .home-column-1-1-2{
        height: 12vh;
        margin-top: 50px;
    }
    .home-column-1-1-2-column{
        width: 300px;
        height: 10vh;
    }
    .home-column-1-1-2-column p{
        font-size: 14px;
    }
    .home-column-1-1-2-column h2{
        font-size: 25px;
    }
    .home-column-2-1{
        display: block;
        width: 100%;
        min-height: 40vh;
        background: var(--background);
        position: relative;
        padding-left: var(--headers-font);
        padding-right: var(--headers-font);
    }
    .home-column-2-1-box{
        position: absolute;
        width: 60%;
        height: 70%;
        left: 10%;
        top: 5%;
        border-radius: 10px;
        background: var(--primary);
    }
    .home-column-2-1-box-2{
        position: absolute;
        width: 60%;
        height: 70%;
        left: 20%;
        top: 15%;
        z-index: 2;
        border-radius: 10px;
        background-image: url("../assets/images/IMG-20230304-WA0022.jpg");
        background-position: center;
        background-size: cover;
    }
    .home-column-2-1-box-3{
        position: absolute;
        width: 60%;
        height: 70%;
        left: 30%;
        top: 25%;
        border-radius: 10px;
        background: var(--pre-color);
        
    }
}

